.order-variants {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow-x: hidden;
}

.slider-btn {
  all: unset;
  font-size: 6vmin;
  cursor: pointer;
  z-index: 100;
  height: 92vmin;
  width: 10%;
  text-align: center;
  border: none;
  background: linear-gradient(
    90deg,
    rgba(26, 43, 78, 1) 80%,
    rgba(0, 212, 255, 0) 100%
  );
}

.next-card {
  background: linear-gradient(
    270deg,
    rgba(26, 43, 78, 1) 80%,
    rgba(0, 212, 255, 0) 100%
  );
}

.variant-card {
  position: absolute;
  left: 30%;
  display: flex;
  flex-direction: column;
  gap: 5vmin;
  width: 40%;
  overflow: hidden;
  padding-top: 3vmin;
  box-sizing: border-box;
  border-radius: 4vmin;
  background-color: var(--dark-1000);
  transition: all 0.5s ease-in-out;
}

.variant-card div {
  display: flex;
  padding: 0 5vmin;
  gap: 2vmin;
  font-size: 3vmin;
}
.variant-card div i {
  font-size: 3vmin;
}

.variant-title {
  padding: 0 5vmin;
  font-size: 5vmin;
  text-align: center;
}

.variant-desc {
  text-align: justify;
  font-size: 3vmin;
  color: var(--accent-main);
}

.variant-action {
  all: unset;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4vmin;
  text-transform: uppercase;
  gap: 2vmin;
  padding: 2vmin;
  background-color: var(--dark-200);
  cursor: pointer;
}
